<template>
  <div class="scan-qr-view">
    <video id="qr-video" ref="videoElem"></video>
  </div>
</template>
<script>
import QrScanner from "qr-scanner";
export default {
  name: "ScanQRView",
  data() {
    return {
      qrScanner: null,
      tempCs: null,
    };
  },
  mounted() {
    this.qrScanner = new QrScanner(
      this.$refs.videoElem,
      (result) => {
        console.log("decoded qr code:", result);
        if (result && result.data) {
          this.$router.push({
            name: "/scan-result",
            params: {
              data: result.data,
            },
          });
        }
      },
      {
        // onDecodeError: (error) => {
        //   console.log("error:", error);
        // },
        highlightScanRegion: true,
        // highlightCodeOutline: true,
        calculateScanRegion: (v) => {
          const smallestDimension = Math.min(v.videoWidth, v.videoHeight);
          // Make scan region smaller to match better small qr codes
          const scanRegionSize = Math.round((2 / 5) * smallestDimension);
          let region = {
            x: Math.round((v.videoWidth - scanRegionSize) / 2),
            y: Math.round((v.videoHeight - scanRegionSize) / 2),
            width: scanRegionSize,
            height: scanRegionSize,
          };
          return region;
        },
      }
    );

    let hasPermission = false;
    // eslint-disable-next-line no-undef
    if (MYAPP) {
      // eslint-disable-next-line no-undef
      hasPermission = MYAPP.checkCameraPermission();
    }

    console.log("hasPermission", hasPermission);

    if (hasPermission) {
      // eslint-disable-next-line no-undef
      const rearCameraId = MYAPP.getRearCameraId();
      console.log("rearCameraId", rearCameraId);
      QrScanner.listCameras(true)
        .then((cameras) => {
          console.log("cameras: ", cameras);
          this.tempCs = cameras;

          this.qrScanner
            .setCamera(
              cameras.find(
                (camera) =>
                  camera.label.toLowerCase().startsWith("camera2") ||
                  camera.label.toLowerCase().startsWith("camera 2")
              )?.id ?? "environment"
            )
            .then(() =>
              this.qrScanner.start().catch((err) => {
                console.log("start error: ", err);
              })
            );
        })
        .catch((err) => {
          console.log("listCameras error: ", err);
        });

      setTimeout(() => {
        this.test();
      }, 5000);
    } else {
      // eslint-disable-next-line no-undef
      MYAPP.showToast("App摄像头权限未打开，请打开后重试。");
    }
  },
  beforeDestroy() {
    if (this.qrScanner) {
      this.qrScanner.stop();
    }
  },
  methods: {
    async test() {
      const cs = [
        "default",
        "user",
        "environment",
        ...this.tempCs.map(({ id }) => id),
      ];
      const $video = document.getElementById("qr-video");

      for (const camera of cs) {
        if (camera !== "default") {
          await this.qrScanner.setCamera(camera);
        }

        console.log(
          `Camera: ${camera}\n${JSON.stringify(
            $video.srcObject.getVideoTracks()[0].getSettings(),
            null,
            4
          )}\n`
        );
      }
    },
  },
};
</script>
<style lang="less">
.scan-qr-view {
  position: relative;

  video {
    position: fixed;
    right: 0;
    bottom: 0;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
  }
}
</style>
