<template>
  <div class="select-wallet">
    <div class="title">Select Wallet</div>
    <div
      class="logos"
      v-for="(item, index) in logos"
      :key="index"
      @click="selectWallet(item)"
    >
      <div class="left">
        <img :src="item.icon" />
        <span>{{ item.name }}</span>
      </div>
      <img class="right" src="@/assets/logos/check.png" v-if="item.checked" />
      <img
        class="right"
        src="@/assets/logos/uncheck.png"
        v-if="!item.checked"
      />
    </div>
    <div class="ok-btn" @click="ok">OK</div>
  </div>
</template>
<script>
export default {
  name: "SelectWalletView",
  data() {
    return {
      logos: [
        {
          icon: require("@/assets/logos/evonet.png"),
          name: "EVONET",
          url: "https://www.evonetglobal.com/",
          checked: true,
        },
        {
          icon: require("@/assets/logos/grabpay.png"),
          name: "GrabPay",
          url: "https://www.grab.com/ph/pay/test_123456789",
          checked: false,
        },
        {
          icon: require("@/assets/logos/paybooc.png"),
          name: "Paybooc",
          url: "https://paybooc.co.kr/app/paybooc/Main.do/random_text_test_example_12345678901234567890",
          checked: false,
        },
      ],
    };
  },
  methods: {
    selectWallet(item) {
      this.logos.forEach((logo) => {
        logo.checked = false;
      });

      item.checked = true;
    },
    ok() {
      const selectedWallet = this.logos.find((f) => f.checked);
      if (selectedWallet) {
        this.$router.push({
          name: "/",
          params: {
            url: selectedWallet.url,
            name: selectedWallet.name,
            logo: selectedWallet.icon,
          },
        });
      }
    },
  },
};
</script>
<style lang="less" scoped>
.select-wallet {
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100vh;

  & > .title {
    font-size: 20px;
    font-weight: 500;
    color: #3d69a2;
    text-align: center;
    padding: 16px 0;
  }

  .logos {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
    cursor: pointer;

    .left {
      display: flex;
      justify-content: center;
      align-items: center;

      & > img {
        width: 48px;
        height: auto;
        margin-right: 8px;
      }
    }

    .right {
      width: 24px;
      height: 24px;
    }
  }

  .ok-btn {
    position: absolute;
    cursor: pointer;
    width: 80%;
    bottom: 48px;
    background: linear-gradient(139deg, #405fa3 0%, #1ed69d 100%);
    box-shadow: 0px 8px 11px 0px #e5f5ff;
    border-radius: 22px 24px 24px 24px;
    font-size: 18px;
    font-weight: 500;
    color: #ffffff;
    padding: 16px 24px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
  }
}
</style>
