import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "@/views/HomeView.vue";
import SelectWalletView from "@/views/SelectWalletView.vue";
import ScanQRView from "@/views/ScanQRView.vue"
import ScanResultView from "@/views/ScanResultView.vue";
import TransactionsView from "@/views/TransactionsView.vue";

Vue.use(VueRouter);

export const routes = [
  { name: "/", path: "/", component: HomeView },
  {
    name: "/select-wallet",
    path: "/select-wallet",
    component: SelectWalletView,
  },
  { name: "/scan-qr-code", path: '/scan-qr-code', component: ScanQRView },
  { name: "/scan-result", path: '/scan-result', component: ScanResultView },
  { name: "/trans", path: '/trans', component: TransactionsView },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
