<template>
  <div id="home">
    <div class="title">Show Payment Code</div>
    <div class="qr-code">
      <div class="code-title">Please show your QR Code to the merchant.</div>
      <vue-qr :text="qrcode" :size="200"></vue-qr>
      <div class="refresh-code" @click="refresh">Refresh</div>
    </div>
    <div class="footer">
      <router-link to="/select-wallet">
        <div class="select-wallet">
          <img :src="logo" />
          <span class="title">{{ name || "Select Wallet" }}</span>
        </div>
      </router-link>
      <router-link to="/scan-qr-code">
        <div class="scan-qr-btn">
          <img src="@/assets/scan.png" />
          <span class="title">Scan QR</span>
        </div>
      </router-link>
      <router-link to="/trans">
        <div class="trans-btn">
          <img src="@/assets/trans.png" />
          <span class="title">Transactions</span>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
import VueQr from "vue-qr";
export default {
  name: "HomeView",
  components: {
    VueQr,
  },
  data() {
    return {
      msg: "默认文字：我是H5的默认文字",
      qrcode: "https://www.evonetglobal.com/",
      name: "EVONET",
      logo: require("@/assets/logos/evonet.png"),
    };
  },
  mounted() {
    window.callJsFunction = this.callJsFunction;

    const walletUrl = this.$route.params.url || this.qrcode;
    if (walletUrl) {
      this.qrcode = walletUrl;
      this.name = this.$route.params.name || this.name;
      this.logo = this.$route.params.logo || this.logo;
    } else {
      console.log("No wallet name");
      // eslint-disable-next-line no-undef
      if (MYAPP) {
        // eslint-disable-next-line no-undef
        MYAPP.showToast("No wallet name");
      }
    }
  },
  methods: {
    callJsFunction(msg) {
      this.msg = "通过调用原生app改变了默认文字：" + msg;
      return "调用h5 js成功，返回值";
    },
    showAndroidToast() {
      // eslint-disable-next-line no-undef
      MYAPP.showToast("H5 js 调用原生app方法");
    },
    refresh() {
      const rand = Math.floor(Math.random() * 100);
      if (this.qrcode.indexOf("@") > -1) {
        this.qrcode =
          this.qrcode.substring(0, this.qrcode.indexOf("@")) + "@" + rand;
      } else {
        this.qrcode = this.qrcode + "@" + rand;
      }
    },
  },
};
</script>

<style lang="less">
#home {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(130deg, #405fa3 0%, #1ed69d 100%);
  box-shadow: 0px 8px 11px 0px #e5f5ff;
  height: 100vh;
  position: relative;
  overflow-y: hidden;

  & > .title {
    font-size: 18px;
    font-weight: 500;
    color: #ffffff;
    padding: 64px 0 16px 0;
  }

  .qr-code {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 32px;
    padding: 16px;
    background: #ffffff;
    border-radius: 22px;

    .code-title {
      font-size: 18px;
      font-weight: 300;
      color: #131a22;
      padding: 16px 24px;
      text-align: center;
    }

    .refresh-code {
      font-size: 16px;
      font-weight: 300;
      color: #131a22;
      padding: 16px 24px;
      text-align: center;
    }
  }

  .footer {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    background: #ffffff;
    border-radius: 20px 20px 0px 0px;
    height: 20%;

    & > a {
      text-decoration: none;
    }

    .select-wallet,
    .scan-qr-btn,
    .trans-btn {
      display: flex;
      flex-direction: column;
      align-items: center;
      cursor: pointer;

      img {
        width: 48px;
        height: auto;
      }

      .title {
        font-size: 14px;
        font-weight: 400;
        color: #505050;
        margin-top: 8px;
      }
    }
  }
}
</style>
